/* main */
.audio_display{
    height: 100%;
    width: 100vw;
    background-size: cover;
    filter: brightness(20%);
}

/* Audio Info */
.audio_info{
    display: flex;
    column-gap: 30px;
    color: white;
    position: absolute;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    top: 40vh;
    left: 3vw;
    animation-name: img_move;
    animation-timing-function: ease-in-out;
    animation-duration: 1s;
    animation-delay: 0;
    animation-direction: normal;
}
.audio_info img{
    height: 300px;
    width: 300px;
    border-radius: 5px;
    animation-name: img_move;
    animation-timing-function: ease-in-out;
    animation-duration: 1s;
    animation-delay: 0;
    animation-direction: normal;
}
.ad_text_div{
    margin-top: 35px;
    display: flex;
    flex-direction: column;
    row-gap: 15px;
} 
.ad_text_name{
    font-size: 75px;
    font-weight: bold;
}
.ad_text_artist{
    color: rgb(225, 225, 225);
    font-family: cursive;
    margin-left: 12px;
    font-size: 25px;
}

/* back bttn */
#ad_back_arrow{
    color: white;
    font-size: x-large;
    background-color: rgb(88, 88, 88);
    border-radius:300px;
    padding:6px 6px 2px 6px ;
    position: absolute;
    top: 3vh;
    right: 2vw;
    cursor: pointer;
}
#ad_back_arrow:hover{
    background-color: rgb(56, 56, 56);
}

@keyframes img_move {
    0%{
        margin-top: 40px;
        opacity: 0%;
    }
    70%{
        margin-top: -20px;
        opacity: 70%;
    }
    100%{
        transform: rotateZ('0');
        opacity: 100%;
        margin-top: 0px;
    }
}

/* CSS for full screen */
@media screen and (min-height:790px){
    .audio_info{
        top: 47vh;
    }
}